'use client';

import { useEffect, useMemo, useState } from 'react';
import { useSearchParams } from 'next/navigation';
import { searchJobs } from '@/lib/api';

type AnyJob = Record<string, any>;

export default function CallReportPage() {
  const [jobs, setJobs] = useState<AnyJob[]>([]);
  const [loading, setLoading] = useState(true);
  const [err, setErr] = useState<string>('');
  const [showDebug, setShowDebug] = useState(false);
  const sp = useSearchParams();

  useEffect(() => {
    (async () => {
      try {
        setLoading(true);
        const res = await searchJobs('@report');
        const rows: AnyJob[] = Array.isArray(res?.rows) ? res.rows : [];
        setJobs(rows);
      } catch (e: any) {
        setErr(e?.message || String(e));
      } finally {
        setLoading(false);
      }
    })();
  }, []);

  useEffect(() => { if (sp.get('debug') === '1') setShowDebug(true); }, [sp]);

  const analysis = useMemo(() => analyze(jobs), [jobs]);
  const toCall = useMemo(() => analysis.matched, [analysis]);

  const printAll = () => {
    const el = document.getElementById('call-report');
    if (!el) return;
    const win = window.open('', '_blank', 'width=1200,height=800');
    if (!win) return;
    const html = `<!doctype html>
<html>
<head>
  <meta charset="utf-8" />
  <title>Call Sheet</title>
  <style>
    @page { size: landscape; margin: 10mm; }
    @media print { @page { size: landscape; margin: 10mm; } }
    body { font-family: Arial, sans-serif; padding: 16px; }
    table { width: 100%; border-collapse: collapse; }
    th, td { border: 1px solid #e5e7eb; padding: 6px 6px; font-size: 12px; vertical-align: top; }
    th { background: #f3f4f6; text-align: left; }
    .num { font-feature-settings: 'tnum' 1; font-variant-numeric: tabular-nums; text-align: right; white-space: nowrap; }
    .paid { text-align:center; font-weight: 800; }
    .paid.yes { color:#059669; }
    .paid.no { color:#94a3b8; }
    ul.reasons { margin: 0; padding-left: 18px; }
    ul.reasons li { margin: 0; }
  </style>
</head>
<body>
  ${el.outerHTML}
  <script>setTimeout(() => window.print(), 50);</script>
</body>
</html>`;
    win.document.open();
    win.document.write(html);
    win.document.close();
  };

  return (
    <div className="light-page">
      <h1>Call Report</h1>
      {err && <div className="err">{err}</div>}
      {loading && <div className="muted">Loading…</div>}

      <div className="bar">
        <div>{analysis.total} jobs • {analysis.counts.finished} finished • {analysis.counts.caped} caped • {analysis.counts.delivered} delivered • <strong>{toCall.length} to call</strong></div>
        <div className="actions">
          <button className="secondary" onClick={() => setShowDebug(s => !s)}>{showDebug ? 'Hide Debug' : 'Show Debug'}</button>
          <button onClick={printAll}>Print</button>
        </div>
      </div>

      {showDebug && (
        <div className="debug">
          <h3>Debug</h3>
          <p className="muted">First 5 normalized rows:</p>
          <table>
            <thead>
              <tr><th>#</th><th>Tag</th><th>Status</th><th>Caping Status</th><th>Webbs Status</th><th>Paid?</th><th>Reason</th></tr>
            </thead>
            <tbody>
              {analysis.peek.map((p, i) => (
                <tr key={i}>
                  <td>{i+1}</td>
                  <td>{p.map['tag'] ?? ''}</td>
                  <td>{p.map['status'] ?? ''}</td>
                  <td>{p.map['capingstatus'] ?? ''}</td>
                  <td>{p.map['webbsstatus'] ?? ''}</td>
                  <td>{String(p.paidAll)}</td>
                  <td>{(p.reasons || []).join(', ')}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      <section id="call-report" className="card">
        <CallTable rows={toCall} />
      </section>

      <style jsx>{`
        .wrap { max-width: 1200px; margin: 18px auto; padding: 8px; font-family: Arial, sans-serif; }
        h1 { margin: 0 0 12px; }
        .card { border: 1px solid #e5e7eb; background: #fff; border-radius: 10px; padding: 12px; margin-bottom: 14px; }
        .bar { display:flex; align-items:center; justify-content:space-between; gap:12px; padding:8px 12px; border:1px solid #e5e7eb; background:#f8fafc; border-radius:10px; margin-bottom:10px; }
        .actions { display:flex; gap:8px; }
        button { padding: 8px 12px; border-radius: 8px; border: 1px solid #cbd5e1; background: #155acb; color: #fff; font-weight: 700; }
        button.secondary { background: #eef2ff; color: #1e40af; }
        .muted { color: #6b7280; }
        .err { color: #b91c1c; margin-bottom: 8px; }

        table { width: 100%; border-collapse: collapse; }
        th, td { border: 1px solid #e5e7eb; padding: 6px 6px; font-size: 12px; vertical-align: top; }
        th { background: #f3f4f6; text-align: left; }
        .num { font-feature-settings: 'tnum' 1; font-variant-numeric: tabular-nums; text-align: right; white-space: nowrap; }
        .paid { text-align:center; font-weight: 800; }
        .paid.yes { color:#059669; }
        .paid.no { color:#94a3b8; }

        @media print {
          @page { size: landscape; margin: 10mm; }
          .wrap { max-width: 100%; }
          .card { break-inside: avoid; }
          button { display: none; }
          .bar { border: none; background: none; padding: 0; }
        }
        .debug { border:1px dashed #94a3b8; padding:8px; border-radius:8px; margin-bottom:10px; background:#fff; }
      `}</style>
    </div>
  );
}

function analyze(jobs: AnyJob[]) {
  const matched: Array<AnyJob & { reasons: string[] }> = [];
  let finished = 0, caped = 0, delivered = 0;
  const peek: Array<{ map: Record<string, any>, reasons: string[], paidAll: boolean }> = [];

  for (const j of jobs) {
    const map = normalizeKeys(j);
    const main = clean(map['status']);
    const cape = clean(map['capingstatus']);
    const webb = clean(map['webbsstatus']);

    const reasons: string[] = [];
    if (hasWord(main, 'finished')) { reasons.push('Meat Ready'); finished++; }
    if (hasWord(cape, 'caped'))    { reasons.push('Cape Ready'); caped++; }
    if (hasWord(webb, 'delivered')){ reasons.push('Webbs Ready'); delivered++; }

    const paidAll = isPaidAll(map);

    if (peek.length < 5) peek.push({ map, reasons, paidAll });
    if (reasons.length) matched.push({ ...j, reasons });
  }

  return {
    total: jobs.length,
    counts: { finished, caped, delivered },
    matched: sortForCalls(matched),
    peek,
  };
}

function CallTable({ rows }: { rows: (AnyJob & { reasons: string[] })[] }) {
  return (
    <table>
      <thead>
        <tr>
          <th style={{width:'70px'}}>Tag</th>
          <th>Customer</th>
          <th style={{width:'150px'}}>Phone</th>
          <th>Reason for Contact</th>
          <th className="num" style={{width:'95px'}}>Proc $</th>
          <th className="num" style={{width:'95px'}}>Spec $</th>
          <th className="num" style={{width:'95px'}}>Total $</th>
          <th style={{width:'70px'}}>Proc Paid</th>
          <th style={{width:'70px'}}>Spec Paid</th>
          <th style={{width:'70px'}}>Paid (All)</th>
          <th>Notes</th>
        </tr>
      </thead>
      <tbody>
        {rows.length === 0 ? (
          <tr>
            <td colSpan={11} style={{textAlign:'center', color:'#6b7280'}}>No rows</td>
          </tr>
        ) : rows.map((j, i) => {
          const map = normalizeKeys(j);

          const proc = suggestedProcessingPrice(map['processtype'], truthy(map['beeffat']), truthy(map['webbsorder']));
          const spec = specialtyPriceFrom(map);
          const total = proc + spec;

          // Paid mapping: exact headers you said + aliases
          const paidAll = isPaidAll(map);
          const paidProc = isPaidProc(map, paidAll);
          const paidSpec = isPaidSpec(map, paidAll);

          return (
            <tr key={(map['tag'] ?? '') + '-' + i}>
              <td>{map['tag'] || ''}</td>
              <td>{map['customer'] || ''}</td>
              <td>{fmtPhone(map['phone'])}</td>
              <td>
                <ul className="reasons">
                  {((j as any).reasons || []).map((r:string, idx:number) => <li key={idx}>{r}</li>)}
                </ul>
              </td>
              <td className="num">{fmtMoney(proc)}</td>
              <td className="num">{fmtMoney(spec)}</td>
              <td className="num"><strong>{fmtMoney(total)}</strong></td>
              <td className={`paid ${paidProc ? 'yes' : 'no'}`}>{paidProc ? '✓' : '▢'}</td>
              <td className={`paid ${paidSpec ? 'yes' : 'no'}`}>{paidSpec ? '✓' : '▢'}</td>
              <td className={`paid ${paidAll ? 'yes' : 'no'}`}>{paidAll ? '✓' : '▢'}</td>
              <td>{map['notes'] || ''}</td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
}

/* ----------------- utils ----------------- */
function normalizeKeys(obj: Record<string, any>) {
  const o: Record<string, any> = {};
  for (const [k, v] of Object.entries(obj)) {
    const nk = String(k).toLowerCase().replace(/[^a-z0-9]+/g, '');
    o[nk] = v;
  }
  return o;
}
function truthy(v: any) {
  if (v === true) return true;
  if (typeof v === 'number') return v > 0;
  const s = String(v ?? '').trim().toLowerCase();
  return ['true','t','yes','y','1','paid','x','✓','✔','on'].includes(s);
}
function isPaidAll(map: Record<string, any>) {
  // Exact headers provided + common variants
  return truthy(map['paid']) || truthy(map['paidinfull']) || truthy(map['paidall']) || truthy(map['paidfull']);
}
function isPaidProc(map: Record<string, any>, paidAll: boolean) {
  return paidAll || truthy(map['paidprocessing']) || truthy(map['processingpaid']) || truthy(map['procpaid']) || truthy(map['paidproc']);
}
function isPaidSpec(map: Record<string, any>, paidAll: boolean) {
  return paidAll || truthy(map['paidspecialty']) || truthy(map['specialtypaid']) || truthy(map['specpaid']) || truthy(map['paidspec']);
}
function clean(v?: any) { return String(v ?? '').trim().toLowerCase(); }
function hasWord(text: string, word: string) { return !!text && text.includes(word); }
function fmtPhone(v?: any) {
  const s = String(v ?? '').replace(/\D+/g, '');
  if (s.length === 10) return `(${s.slice(0,3)}) ${s.slice(3,6)}-${s.slice(6)}`;
  if (s.length === 7)  return `${s.slice(0,3)}-${s.slice(3)}`;
  return String(v ?? '');
}
function sortForCalls<T extends Record<string, any>>(rows: T[]) {
  const num = (x: any) => {
    const n = parseInt(String(x||'').replace(/[^\d]/g,''),10);
    return Number.isFinite(n) ? n : 0;
  };
  return [...rows].sort((a,b) => {
    const ta = num((a as any).tag ?? (a as any)['Tag']);
    const tb = num((b as any).tag ?? (b as any)['Tag']);
    if (ta !== tb) return ta - tb;
    const ca = String((a as any).customer ?? (a as any)['Customer'] ?? '').toLowerCase();
    const cb = String((b as any).customer ?? (b as any)['Customer'] ?? '').toLowerCase();
    return ca.localeCompare(cb);
  });
}
function toInt(val: any) {
  const n = parseInt(String(val ?? '').replace(/[^0-9]/g, ''), 10);
  return Number.isFinite(n) && n > 0 ? n : 0;
}
function suggestedProcessingPrice(proc: any, beef: boolean, webbs: boolean) {
  const p = String(proc || '').toLowerCase();
  let base = 0;
  if (p.includes('cape') && !p.includes('skull')) base = 150;
  else if (p.includes('standard') || p.includes('skull') || p.includes('euro')) base = 130;
  return base + (beef ? 5 : 0) + (webbs ? 20 : 0);
}
function specialtyPriceFrom(map: Record<string, any>) {
  const hasSpec = truthy(map['specialtyproducts']);
  if (!hasSpec) return 0;
  const ss  = toInt(map['summersausagelbs']);
  const ssc = toInt(map['summersausagecheeselbs']);
  const jer = toInt(map['slicedjerkylbs']);
  return ss * 4.25 + ssc * 4.60 + jer * 15.0;
}
function fmtMoney(n: number) { return (Number.isFinite(n) ? n : 0).toFixed(2); }
