// lib/api.ts
// Lightweight client for the Apps Script endpoints

export type CutsBlock = {
  'Hind - Steak'?: boolean;
  'Hind - Roast'?: boolean;
  'Hind - Grind'?: boolean;
  'Hind - None'?: boolean;
  'Front - Steak'?: boolean;
  'Front - Roast'?: boolean;
  'Front - Grind'?: boolean;
  'Front - None'?: boolean;
};

export type Job = {
  tag: string;

  confirmation?: string;

  customer?: string;
  phone?: string;
  email?: string;
  address?: string;
  city?: string;
  state?: string;
  zip?: string;

  county?: string;
  dropoff?: string; // yyyy-mm-dd
  sex?: '' | 'Buck' | 'Doe';
  processType?: '' | 'Standard Processing' | 'Caped' | 'Skull-Cap' | 'European';

  status?: string;
  capingStatus?: string;
  webbsStatus?: string;

  steak?: string;
  steakOther?: string;
  burgerSize?: string;
  steaksPerPackage?: string;
  beefFat?: boolean;

  hindRoastCount?: string;
  frontRoastCount?: string;

  hind?: CutsBlock;
  front?: CutsBlock;

  backstrapPrep?: '' | 'Whole' | 'Sliced' | 'Butterflied';
  backstrapThickness?: '' | '1/2"' | '3/4"' | 'Other';
  backstrapThicknessOther?: string;

  specialtyProducts?: boolean;
  summerSausageLbs?: string | number;
  summerSausageCheeseLbs?: string | number;
  slicedJerkyLbs?: string | number;
  specialtyPounds?: string;

  notes?: string;

  webbsOrder?: boolean;
  webbsFormNumber?: string;
  webbsPounds?: string;

  // pricing (optional — UI can compute)
  priceProcessing?: number | string;
  priceSpecialty?: number | string;
  price?: number | string;

  // paid flags (legacy + split)
  Paid?: boolean;
  paid?: boolean;
  paidProcessing?: boolean;
  paidSpecialty?: boolean;

  // NEW: picked-up flags + timestamps
  ['Picked Up - Processing']?: boolean;
  ['Picked Up - Processing At']?: string | Date | '';
  ['Picked Up - Cape']?: boolean;
  ['Picked Up - Cape At']?: string | Date | '';
  ['Picked Up - Webbs']?: boolean;
  ['Picked Up - Webbs At']?: string | Date | '';

  // NEW: call tracking
  ['Call Attempts']?: number;
  ['Last Called At']?: string | Date | '';
  ['Last Called By']?: string;
  ['Last Call Outcome']?: string;

  ['Notified Ready At']?: string | Date | '';
};

type ApiResult<T> = { ok: true } & T;
type ApiError = { ok: false; error: string };

const BASE =
  process.env.NEXT_PUBLIC_GAS_URL?.trim() ||
  ''; // e.g. https://script.google.com/macros/s/AK.../exec

const TOKEN = process.env.NEXT_PUBLIC_API_TOKEN?.trim() || '';

function q(params: Record<string, string | number | undefined>) {
  const usp = new URLSearchParams();
  Object.entries(params).forEach(([k, v]) => {
    if (v !== undefined && v !== null && v !== '') usp.set(k, String(v));
  });
  if (TOKEN) usp.set('token', TOKEN);
  return usp.toString();
}

async function jget<T = any>(pathParams: Record<string, any>) {
  if (!BASE) throw new Error('Missing NEXT_PUBLIC_GAS_URL');
  const url = `${BASE}?${q(pathParams)}`;
  const r = await fetch(url, { cache: 'no-store' });
  const data = await r.json();
  return data as T | ApiError;
}

async function jpost<T = any>(pathParams: Record<string, any>, body?: any) {
  if (!BASE) throw new Error('Missing NEXT_PUBLIC_GAS_URL');
  const url = `${BASE}?${q(pathParams)}`;
  const r = await fetch(url, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(body || {}),
  });
  const data = await r.json();
  return data as T | ApiError;
}

/* --------- exported helpers --------- */

export async function ping() {
  return jget<ApiResult<{ pong: string }>>({ action: 'ping' });
}

export async function getJob(tag: string) {
  return jget<ApiResult<{ exists: boolean; job?: Job }>>({
    action: 'get',
    tag,
  });
}

export async function searchJobs(qstr: string) {
  return jget<ApiResult<{ rows: Partial<Job>[] }>>({
    action: 'search',
    q: qstr,
  });
}

export async function saveJob(job: Partial<Job> & { tag: string }) {
  return jpost<ApiResult<{ row: number }>>({ action: 'save' }, { job });
}

export async function progress(payload: { tag: string }) {
  return jpost<ApiResult<{ nextStatus: string | null }>>(
    { action: 'progress' },
    payload
  );
}

// NEW: call logging endpoint
export async function logCall(params: {
  tag: string;
  by?: string;
  outcome?: string;
}) {
  return jpost<ApiResult<{ attempts: number; lastCalledAt: string }>>(
    { action: 'call' },
    params
  );
}


